	module.exports = {"data":{"markdownRemark":{"html":"<p>ボトル半分でおしまい？私はもう３本目よー 有名なのでご存知の方も多いかと思われますが、スピリタスはウォッカの仲間でアルコール度数が96度もある世界最強の酒です。 ぶっちゃけ、ほとんどアルコールです。アルコールが強すぎて“お酒は二十歳から”よりもまず先に“火気厳禁”が来るほどです。比較的入手しやすい(輸入ウォッカ扱ってる酒屋なら大抵店頭に並んでいる)上、2000円も出せば購入できるので物好きな方は買ってみてはいかがでしょうか？ちなみに、匂いを嗅ぐだけで脳内になぜか小学校の頃の予防接種のいや～な思い出が蘇ってきます。グラスに注ぐと氷が沈むと言う怪現象もご覧になれます。 よい子はボトル半分すら飲んではいけません。ボトル半分どころか、グラス半分も飲めればもう神認定です。霊夢が異常なだけです。</p>","frontmatter":{"cover":null,"date":"2004-12-11T00:57:00+09:00","strDate":"2004/12/10","title":"一気飲み競争"}}},"pathContext":{"path":"/ja/blog/2004/12/11-一気飲み競争/","langKey":"ja"}}

